
.comment {
  word-wrap: break-word;
}
.comment p {
    text-align: left;
    margin-bottom: 0.5rem;
}
.comment code {
    background-color: #3e3e7b;
    color: white;
}
.comment ul {
    list-style-type: initial;
    padding-left: 1rem;
}
.comment ul li:first-child {
      font-weight: 400;
      background: none;
      box-shadow: none;
}
.orangered-color {
  color: #ff9453;
  font-weight: bold;
}
.periwinkle-color {
  color: #00ccff;
  font-weight: bold;
}
.controversial {
  font-weight: 700;
  color: #ff5600;
}
p {
  font-size: 17px;
  color: #404a58;
}
a {
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}
a:hover {
    text-decoration: none;
    background-color: rgba(0, 150, 255, 0.1);
}



