
.progress {
  line-height: 2rem;
  font-size: 1rem;
  background: rgba(0, 0, 0, 0.5);
  margin: 0 auto;
  width: 500px;
  max-width: 100%;
  border: 1px solid black;
}
.progress-bar {
  background: -webkit-linear-gradient(335deg, #ff79aa, #b743ff);
  background: linear-gradient(115deg, #ff79aa, #b743ff);
  height: 2rem;
}



