
label {
  margin-bottom: 0;
}
canvas {
  margin-bottom: 1rem;
}
.switch {
  position: relative;
  display: block;
  width: 60px;
  height: 34px;
  margin: auto;
}
.chart-switch {
  margin-bottom: 1rem;
}
.chart-switch .col {
    -webkit-box-pack: center;
    -webkit-justify-content: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.chart-switch p {
    margin-bottom: 0.25rem;
    margin-right: 0;
    margin-left: 0;
}

/* Hide default HTML checkbox */
.switch input {
  display: none;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.2);
  height: 31px;
  -webkit-transition: .25s;
  transition: .25s;
}
.slider::before {
    position: absolute;
    content: "";
    height: 21px;
    width: 21px;
    left: 6px;
    bottom: 5px;
    background-color: rgba(255, 255, 255, 0.7);
    -webkit-transition: .25s;
    transition: .25s;
}
input:checked + .slider {
  background: -webkit-linear-gradient(315deg, #00bec7, #0073e8);
  background: linear-gradient(135deg, #00bec7, #0073e8);
}
input:checked + .slider::before {
    background-color: white;
}
input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}
input:checked + .slider:before {
  -webkit-transform: translateX(26px);
      -ms-transform: translateX(26px);
          transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}
.slider.round::before {
    border-radius: 50%;
}
@media (min-width: 992px) {
.chart-switch p {
    margin-bottom: 0;
    margin-right: 1rem;
    margin-left: 1rem;
}
.switch-wrapper {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
}
.switch {
    display: inline-block;
    margin: 0;
}
}



